<script>
import { ref } from "vue";
import { reactive } from "vue";

export default {
    setup() {
        return {
            formRef: ref(),
            form: reactive({
                name: "",
                skinUrl: "",
                skinObj: "",
                isLicensed: false,
                noLawProblem: false,
            }),
            rules: {
                name: [{ required: true, message: "请填写您的 Minecraft ID" }],
                skin: [{ required: true, message: "请上传您的皮肤文件" }],
                isLicensed: [
                    { required: true, message: "请授权我们存放您的数据" },
                ],
                noLawProblem: [
                    {
                        required: true,
                        message: "请确认您拥有您所上传皮肤文件的使用权或版权",
                    },
                ],
            },
            customRequest: (option) => {
                // eslint-disable-next-line
                const { _onProgress, _onError, onSuccess, _fileItem, _name } =
                    option;
                onSuccess("");
            },
            checks: {
                check1: false,
                check2: false,
            },
        };
    },
    methods: {
        vallidate() {
            // Verify Part: 1
            const [form1, form2, form3] = [
                this.form.name,
                this.form.isLicensed,
                this.form.noLawProblem,
            ];
            if (form1 != "" && form2 && form3) {
                this.formRef.clearValidate([
                    "name",
                    "isLicensed",
                    "noLawProblem",
                ]);
                this.checks.check1 = true;
            } else {
                this.formRef.validateField([
                    "name",
                    "isLicensed",
                    "noLawProblem",
                ]);
                this.checks.check1 = false;
            }
            // Verify Part: 2
            this.vaildateSkinImg();
            // Final Return
            return this.checks.check1 && this.checks.check2;
        },
        vaildateSkinImg(fileItem) {
            if (fileItem) {
                if (fileItem.url != undefined) {
                    this.formRef.clearValidate("skin");
                    this.checks.check2 = true;
                    [this.form.skinObj, this.form.skinUrl] = [
                        fileItem.file,
                        fileItem.url,
                    ];
                    setTimeout(() => this.vaildateSkinImg(), 100);
                    console.log(1);
                } else {
                    this.formRef.validateField("skin");
                    this.checks.check2 = false;
                    this.form.skinUrl = null;
                    console.log(2);
                }
            } else {
                if (
                    this.form.skinUrl != (void 0 || null) &&
                    this.form.skinUrl.length > 0
                ) {
                    this.formRef.clearValidate("skin");
                    this.checks.check2 = true;
                    console.log(3);
                } else {
                    this.formRef.validateField("skin");
                    this.checks.check2 = false;
                    console.log(4);
                }
            }
        },
        handleButtonClick() {
            const check = this.vallidate();
            if (check == true) {
                this.$emit("step1Passed", [
                    this.form.name,
                    this.form.skinUrl,
                    this.form.skinObj,
                ]);
            } else {
                this.vallidate();
                this.vaildateSkinImg();
            }
        },
    },
};
</script>

<template>
    <div id="configContainer">
        <a-form :model="form" :rules="rules" ref="formRef">
            <a-form-item
                field="name"
                label="你的 Minecraft ID"
                tooltip="用于绑定ID与皮肤文件">
                <a-input
                    type="username"
                    id="username"
                    v-model="form.name"
                    placeholder="在这里输入你的 Minecraft ID" />
            </a-form-item>
            <a-form-item field="skin" label="皮肤文件" tooltip="需要符合规范哦">
                <a-upload
                    :limit="1"
                    :custom-request="customRequest"
                    @success="vaildateSkinImg"></a-upload>
            </a-form-item>
            <a-form-item field="isLicensed">
                <a-checkbox v-model="form.isLicensed">
                    我授权存放我的皮肤文件于此工具的服务器上
                </a-checkbox>
            </a-form-item>
            <a-form-item field="noLawProblem">
                <a-checkbox v-model="form.noLawProblem">
                    我确认我有所上传的皮肤的版权或使用权
                </a-checkbox>
            </a-form-item>
            <a-form-item>
                <a-button type="primary" @click="handleButtonClick"
                    >确认</a-button
                >
            </a-form-item>
        </a-form>
    </div>
</template>

<style scoped lang="less">
div#configContainer {
    display: block;
}
</style>
