<script>
import { ref, onMounted } from "vue";
import ConfigForm from "@/components/ConfigForm.vue";

export default {
    setup() {
        function initCardBodyHeight() {
            const card = document.querySelectorAll("div.arco-card")[0];
            const configContainer = document.querySelector(
                "div#configContainer"
            );
            configContainer.style.height =
                card.clientHeight - 46 - 16 * 2 - 40 + "px";
        }
        onMounted(() => {
            initCardBodyHeight();
        });
        return {
            currentStep: ref(1),
            sec: ref(10),
            currentStatus: ref("wait"),
            currentStep2Type: ref("warning"),
            disableNextButton: ref(true),
            clearForm: ref(true),
            step3data: ref(null),
        };
    },
    emits: ["handleStep1Passed", "handlePostToServer", "handleReset"],
    methods: {
        nextStep() {
            this.currentStep = Math.min(3, this.currentStep + 1);
        },
        resetStep() {
            this.currentStep = 1;
            this.disableNextButton = true;
            this.$forceUpdate();
            this.clearForm = false;
            this.$nextTick(() => {
                this.clearForm = true;
            });
        },
        handleStep1Passed(args) {
            this.nextStep();
            this.$nextTick(() => {
                this.$emit("handleStep1Passed", args);
            });
            this.funcInStep2();
        },
        handleResetUploadStep() {
            // TODO: 不刷新页面的情况下重置步骤
            this.resetStep();
            this.$emit("handleReset", 1);
            // location.reload();
        },
        funcInStep2() {
            const secInterval = setInterval(() => {
                this.sec = Math.max(0, this.sec - 1);
            }, 1000);
            setTimeout(() => {
                this.disableNextButton = false;
                this.currentStep2Type = "success";
                clearInterval(secInterval);
            }, 10000);
        },
        handlePost() {
            this.$emit("handlePostToServer", 1);
            this.nextStep();
        },
        Step3Update(status, data) {
            this.currentStatus = status;
            const cmd = data.data.url
            this.step3data = `/skin url ${cmd}`;
        },
        copyToPB(str) {
            try {
                navigator.clipboard.writeText(str);
            } catch {
                // 备用方案
                const copyText = document.getElementById("copy-text");
                copyText.value = str;
                copyText.select();
                document.execCommand("copy");
            }
        },
    },
    components: {
        LyConfigForm: ConfigForm,
    },
};
</script>

<template>
    <a-card title="上传皮肤">
        <template #extra>
            <a-link @click="handleResetUploadStep">
                <icon-sync />
                重置上传步骤
            </a-link>
        </template>
        <a-steps type="arrow" :small="true" :current="currentStep">
            <a-step>1. 填写信息</a-step>
            <a-step>2. 预览</a-step>
            <a-step
                :class="{
                    red: currentStep == 3 && currentStatus == 'error',
                    yellow: currentStep == 3 && currentStatus == 'wait',
                    green: currentStep == 3 && currentStatus == 'finish',
                }"
                >3. 指令生成</a-step
            >
        </a-steps>
        <br />
        <div id="configContainer">
            <div class="steps" id="step1" v-if="currentStep == 1">
                <ly-config-form
                    v-if="clearForm"
                    @step1Passed="handleStep1Passed" />
            </div>
            <div class="steps" id="step2" v-if="currentStep == 2">
                <a-result :status="currentStep2Type" title="是否确认">
                    <template #subtitle>
                        <span v-if="sec > 0"
                            >请先在右侧预览窗口确认，将在
                            {{ sec }} 秒钟后允许上传</span
                        >
                        <span v-if="!sec > 0">确认请点击下方按钮上传</span>
                    </template>
                    <template #extra>
                        <a-space>
                            <a-button
                                type="primary"
                                :loading="disableNextButton"
                                @click="handlePost">
                                <template #icon>
                                    <icon-upload />
                                </template>
                                上传
                            </a-button>
                        </a-space>
                    </template>
                </a-result>
            </div>
            <div class="steps" id="step3" v-if="currentStep == 3">
                <div id="step3-error" v-if="currentStatus == 'error'">
                    <a-result status="error" title="发生错误">
                        <template #subtitle>
                            <br />
                            上传皮肤时发生了错误，请联系网站管理员以获得支持
                            <br />
                            或者点击本卡片右上角按钮可重头开始
                        </template>
                    </a-result>
                </div>
                <div id="step3-wait" v-if="currentStatus == 'wait'">
                    <a-result :status="null" title="等待生成指令中...">
                        <template #icon>
                            <a-spin size="40"></a-spin>
                        </template>
                    </a-result>
                </div>
                <div id="step3-finish" v-if="currentStatus == 'finish'">
                    <a-result status="success" title="上传成功">
                        <template #subtitle>
                            <br />
                            使用下方指令来换皮肤
                            <br />
                            {{ step3data }}
                            <br />
                            点击下方按钮可复制指令到剪贴板哦
                        </template>
                        <template #extra>
                            <br />
                            <a-space>
                                <a-button
                                    type="primary"
                                    @click="copyToPB(step3data)"
                                    >复制指令</a-button
                                >
                            </a-space>
                        </template>
                    </a-result>
                </div>
            </div>
        </div>
    </a-card>
    <span id="copy-text" style="display: none"></span>
</template>

<style scoped lang="less">
div.arco-card {
    height: 100%;
    box-shadow: 4px 4px 10px rgb(var(--gray-2));
    transition-property: all;
    transition: transform 0.3s ease-in-out;
    border-radius: 5px;
}

a.arco-link {
    position: absolute;
    right: 5px;
    font-size: 12px;
    svg {
        font-size: 12px;
        margin-right: 2px;
    }
}

div#configContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    div#step1 {
        width: 100%;
        height: 80%;
    }
}

.red {
    background-color: rgb(var(--red-5)) !important;
}
.yellow {
    background-color: rgb(var(--orange-5)) !important;
}
.green {
    background-color: rgb(var(--green-5)) !important;
}
</style>
