<script>
import { onMounted } from "vue";
import SkinViewCanvas from "./SkinViewCanvas.vue";
import SkinViewSetting from "./SkinViewSetting.vue";

export default {
    setup() {
        function initCardBodyHeight() {
            const card = document.querySelectorAll("div.arco-card")[2];
            const cardBody = document.querySelectorAll("div.arco-card-body")[2];
            cardBody.style.height = card.clientHeight - 46 - 16 * 2 + "px";
        }
        onMounted(() => {
            initCardBodyHeight();
        });
    },
    data() {
        return {
            isSkinLoaded: false,
            content: "还未加载皮肤，请先于左侧卡片加载",
        };
    },
    components: {
        LySkinViewCanvas: SkinViewCanvas,
        LySkinViewSetting: SkinViewSetting,
    },
    methods: {
        loadSkin(args) {
            this.isSkinLoaded = true;
            this.$nextTick(() => {
                this.$refs.canvas.loadSkin(args);
                this.$refs.setting.loaded();
            });
        },
        reset() {
            this.isSkinLoaded = false;
            this.$refs.setting.reset();
            this.content = "还未加载皮肤，请先于左侧卡片加载";
        },
        handleArgsChange(args) {
            this.$refs.canvas.reloadSkin(args);
        },
        close() {
            this.$refs.canvas.closeView();
            this.content = "为节省性能，已关闭皮肤预览";
            this.isSkinLoaded = false;
        },
    },
};
</script>

<template>
    <a-card title="皮肤预览">
        <div id="skinContainer">
            <div class="view" v-if="!isSkinLoaded">
                <a-empty :description="content" />
            </div>
            <div class="view" v-if="isSkinLoaded">
                <ly-skin-view-canvas ref="canvas" />
            </div>
            <div id="skinControl">
                <ly-skin-view-setting
                    ref="setting"
                    @argsChange="handleArgsChange" />
            </div>
        </div>
    </a-card>
</template>

<style scoped lang="less">
div.arco-card {
    height: 100%;
    box-shadow: 4px 4px 10px rgb(var(--gray-2));
    transition-property: all;
    transition: transform 0.3s ease-in-out;
    border-radius: 5px;
}

div.arco-card-body {
    height: 100%;
}

div#skinContainer {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 40px;
    grid-auto-columns: 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "skinView"
        "skinControl";
    div.view {
        grid-area: skinView;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    div#skinControl {
        grid-area: skinControl;
    }
}
</style>
