<script>
import { ref } from "vue";
import axios from "axios";

import PageHeader from "./components/PageHeader.vue";
import SkinViewCard from "./components/SkinViewCard.vue";
import ConfigCard from "./components/ConfigCard.vue";
import HistoryCard from "./components/HistoryCard.vue";

export default {
    name: "App",
    components: {
        LyPageHeader: PageHeader,
        LySkinView: SkinViewCard,
        LyConfigCard: ConfigCard,
        LyHistoryCard: HistoryCard,
    },
    setup() {
        const skinConfigs = {
            name: "",
            skinUrl: "",
            skinObj: null,
        };
        const visible = ref(false);
        const handleOpen = () => {
            visible.value = true;
        };
        const handleClose = () => {
            visible.value = false;
        };
        return {
            skinConfigs,
            visible,
            handleOpen,
            handleClose,
        };
    },
    methods: {
        // eslint-disable-next-line
        handleHistoryQuestion(_ev) {
            this.handleOpen();
        },
        Step1Passed(args) {
            [
                this.skinConfigs.name,
                this.skinConfigs.skinUrl,
                this.skinConfigs.skinObj,
            ] = args;
            this.$nextTick(() => {
                this.$refs.skinViewEl.loadSkin(args[1]);
            });
        },
        // eslint-disable-next-line
        ResetStep(_ev) {
            this.$refs.skinViewEl.reset();
        },
        handlePost() {
            this.$refs.skinViewEl.close()
            const formData = new FormData();
            formData.append("text_data", this.skinConfigs.name);
            formData.append(
                "image",
                this.skinConfigs.skinObj,
                this.skinConfigs.skinObj.name
            );
            axios
                // eslint-disable-next-line
                .post(`http://${server_host}:${server_port}/addSkin`, formData, {
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                })
                .then((response) => {
                    this.$refs.configCard.Step3Update("finish", response);
                })
                .catch((error) => {
                    this.$refs.configCard.Step3Update("error", void 0);
                    console.error("Error on post data to server:", error);
                });
        },
    },
};
</script>

<template>
    <ly-page-header />
    <div id="container">
        <div id="ConfigCard">
            <ly-config-card
                ref="configCard"
                @handleStep1Passed="Step1Passed"
                @handlePostToServer="handlePost"
                @handleReset="ResetStep" />
        </div>
        <div id="History">
            <ly-history-card @QuestionButtonClick="handleHistoryQuestion" />
        </div>
        <div id="SkinView">
            <ly-skin-view ref="skinViewEl" />
        </div>
    </div>
    <a-drawer
        popup-container="#container"
        placement="bottom"
        @ok="handleClose"
        @cancel="handleClose"
        :visible="visible"
        :hide-cancel="true"
        :mask-closable="true"
        :height="247">
        <template #title>
            常见问题：为什么我之前上传的历史皮肤找不到（消失）了?
        </template>
        <div>
            本站采用了 Cookie
            作为身份记录，基于此技术，浏览器可以在你打开页面的时候自动向服务器获取历史皮肤。
            <br />
            如果你清除了浏览器
            Cookie、更换了浏览器访问、更换了设备访问，或者是上传的皮肤在 15
            天前的，将无法在历史皮肤中找到。
            <br /><br />
            如需要查询未显示的皮肤，请联系网站管理员到后台查询
            <br />
            本站对于同一个 Minecraft ID
            将保留最后上传的一张皮肤图片，直到你重新上传覆盖或管理员清理
        </div>
    </a-drawer>
</template>

