<script>
export default {
    data() {
        return {
            skinViewer: null,
            skinFilePath: "",
            skinAnimation: null,
        };
    },
    methods: {
        init() {
            const skinViewCanvas = document.querySelector("canvas#skinViewEl");
            // eslint-disable-next-line
            this.skinViewer = new skinview3d.SkinViewer({
                canvas: skinViewCanvas,
                width: skinViewCanvas.clientWidth,
                height: skinViewCanvas.clientHeight,
            });
            this.skinViewer.loadSkin(null);
            this.skinViewer.loadCape(null);
            this.skinViewer.zoom = 0.7;
            this.skinViewer.background = 0xffffff;
            // eslint-disable-next-line
            this.skinAnimation = new skinview3d.IdleAnimation();
            this.skinViewer.animation = this.skinAnimation;
        },
        loadSkin(args) {
            this.$nextTick(() => {
                this.init();
                this.skinFilePath = args;
                this.skinViewer.loadSkin(this.skinFilePath, {
                    model: "auto-detect",
                });
            });
        },
        reloadSkin(args) {
            this.skinViewer.loadSkin(this.skinFilePath, args[0]);
            this.skinViewer.autoRotate = args[1]["rotate"];
            this.skinViewer.animation = args[1]["idleAnimation"]
                ? this.skinAnimation
                : null;
        },
        resetView() {
            this.$nextTick(() => {
                this.skinViewer = null;
                this.isSkinLoaded = false;
            });
        },
        closeView() {
            this.$nextTick(() => {
                this.skinViewer = null;
            });
        },
    },
};
</script>

<template>
    <div id="skinViewContainer">
        <canvas id="skinViewEl"></canvas>
    </div>
</template>

<style scoped lang="less">
div#skinViewContainer {
    height: 100%;
    width: 100%;
    canvas {
        height: 100%;
        width: 100%;
    }
}
</style>
