<script>
export default {
    setup() {
        return {
            skinLoadArgs: {
                model: "auto-detect",
            },
            displayArgs: {
                idleAnimation: true,
                rotate: false,
            },
            isDisable: {
                radio: true,
                button: true,
            },
        };
    },
    methods: {
        emit() {
            this.$emit("argsChange", [this.skinLoadArgs, this.displayArgs]);
        },
        // eslint-disable-next-line no-unused-vars
        handleSkinModelChange(value, _ev) {
            this.skinLoadArgs.model = value;
            this.emit();
        },
        // eslint-disable-next-line no-unused-vars
        handleIdelAnimationChange(value, _ev) {
            this.displayArgs.idleAnimation = value;
            this.emit();
        },
        // eslint-disable-next-line no-unused-vars
        handleRotateChange(value, _ev) {
            this.displayArgs.rotate = value;
            this.emit();
        },
        loaded() {
            this.isDisable.radio = false;
            this.isDisable.button = false;
            this.$forceUpdate();
        },
        reset() {
            this.isDisable.radio = true;
            this.isDisable.button = true;
            this.$forceUpdate();
        },
    },
};
</script>

<template>
    <div id="svsContainer">
        <div class="settingItem">
            <span class="settingName">皮肤类型</span>
            <a-radio-group
                type="button"
                default-value="auto-detect"
                :disabled="isDisable.radio"
                @change="handleSkinModelChange">
                <a-radio value="auto-detect">自动选择</a-radio>
                <a-radio value="default">Default</a-radio>
                <a-radio value="slim">Slim</a-radio>
            </a-radio-group>
        </div>
        <div class="settingItem">
            <span class="settingName">其他设置</span>
            <span id="tags" :class="{ disable: isDisable.button }">
                <a-tag
                    :class="{ disable: isDisable.button }"
                    color="green"
                    :checkable="!isDisable.button"
                    :default-checked="displayArgs.idleAnimation"
                    @check="handleIdelAnimationChange"
                    >播放动画</a-tag
                >
                <span class="splitEl"></span>
                <a-tag
                    :class="{ disable: isDisable.button }"
                    :color="isDisable.button ? '' : 'green'"
                    :checkable="!isDisable.button"
                    :default-checked="displayArgs.rotate"
                    @check="handleRotateChange"
                    >旋转</a-tag
                >
            </span>
        </div>
    </div>
</template>

<style scoped lang="less">
div#svsContainer {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    div.settingItem {
        margin-right: 12px;
        span.settingName {
            margin-right: 6px;
        }
        span#tags {
            display: inline-flex;
            padding: 4px;
            line-height: 26px;
            background-color: var(--color-fill-2);
            border-radius: var(--border-radius-small);
            span.arco-tag {
                margin-right: 4px;
                cursor: pointer;
                &.disable {
                    opacity: 0.75;
                    cursor: not-allowed;
                }
            }
            span.splitEl::before {
                content: "";
                left: -2px;
                display: block;
                width: 1px;
                height: 14px;
                background-color: var(--color-neutral-3);
                transform: translateY(41%);
            }
        }
        .disable {
            opacity: 0.75;
            cursor: not-allowed;
        }
    }
}
</style>
